
button{
    	color: #54ADF3;
    	border: 1px solid #54ADF3;
	    outline: none;
	    background: #54ADF3;
}
.panel{
		-webkit-box-shadow: 0px 0 20px 5px rgba(102,168,255,.3);
		        box-shadow: 0px 0 20px 5px rgba(102,168,255,.3);
}
.errorTip{
        background-color: rgba(102, 102, 102, 0.9);
        line-height: 30px;
        top:50%;
        left: 50%;
        margin: 0 0 0 -25%;
}
.showError-enter-active,.showError-leave-active{
        -webkit-transition: 0.5s all ease;
        transition: 0.5s all ease;
        opacity: 1;
}
.showError-enter,.showError-leave-active{
        opacity: 0;
}



